import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CardSearchApiService {
  apiUrl = 'https://api.cncbogs.de/image/process';

  constructor(private http: HttpClient) { }
  
  cardSearch( image ) {
    return this.http.post(`${this.apiUrl}`, { "image": image, });
  }
}
