import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { YuGiOhProCard } from 'src/app/models/YuGiOhProCard';
import { CardSearchApiService } from 'src/app/service/cardsearch/cardsearchapi.service';
import { YuGiOhProApiService } from 'src/app/service/yugiohpro/yugiohproapi.service';

@Component({
  selector: 'app-camera',
  templateUrl: './camera.component.html',
  styleUrls: ['./camera.component.css']
})
export class CameraComponent implements AfterViewInit, OnInit {
  @ViewChild("video")
  public video: ElementRef;

  @ViewChild("previewPicture")
  public previewPicture: ElementRef;

  @ViewChild("idHash")
  public idHashPicture: ElementRef;

  @ViewChild("idMatch")
  public idMatchPicture: ElementRef;
  

  currentFacingMode: string;
  size: number;
  isWorking: boolean;


  constructor(
    private yugiohproApi: YuGiOhProApiService,
    private cardsearchApi: CardSearchApiService,
    ) { }

  ngOnInit(): void {
    this.currentFacingMode="environment"
    this.size=1920
    navigator.mediaDevices.getUserMedia({
      audio: false,
      video: {
        width: { ideal: this.size },
        height: { ideal: this.size },
        facingMode: this.currentFacingMode,
      }
    })
  }
  
  ngAfterViewInit(): void {
    if(
      navigator.mediaDevices && 
      navigator.mediaDevices.getUserMedia &&
      navigator.mediaDevices.enumerateDevices
    ) {
      navigator.mediaDevices.getUserMedia(
        {
          audio: false,
          video: {
            width: { ideal: this.size },
            height: { ideal: this.size },
            facingMode: this.currentFacingMode,
          }
        }
      ).then(stream => {
          this.video.nativeElement.srcObject = stream;
          this.video.nativeElement.play();
      });
    }
  }
  
  takeSnapshot() {
    this.isWorking = true;
    var canvas = document.createElement('canvas');

    var width = this.video.nativeElement.videoWidth;
    var height = this.video.nativeElement.videoHeight;

    canvas.width = width;
    canvas.height = height;

    var context_canvas = canvas.getContext('2d');
    context_canvas.drawImage(this.video.nativeElement, 0, 0, width, height);

    this.previewPicture.nativeElement.width = width;
    this.previewPicture.nativeElement.height = height;

    var context_picture = this.previewPicture.nativeElement.getContext('2d');
    context_picture.drawImage(this.video.nativeElement, 0, 0, width, height);

    this.getCanvasURL(canvas)
    .then(
      async data_url => {
        this.cardsearchApi.cardSearch(data_url).toPromise()
        .then(
          async resp => {
            let id_hash   = resp["best_hash_id"];
            let id_match  = resp["best_match_id"];
            let state = await Promise.all([ this.getCardData(id_hash, this.idHashPicture), this.getCardData(id_match, this.idMatchPicture) ])
            let result = ""
            state.forEach(x => {
              result += `${x[0]}: ${x[1]}\n`
            })
            alert(result)
            
          }
        ).finally(
          () => {
            this.isWorking = false;
          }
        )
      }
    );
  }

  getCanvasURL( canvas ) {
    return new Promise(
      function (resolve) {
        resolve(canvas.toDataURL());
      }
    );
  }

  async getCardData( id, img:ElementRef ) {
    return Promise.all([
      this.yugiohproApi.getCardInfo(id)
      .toPromise()
      .then(
        (data: YuGiOhProCard) => {
          img.nativeElement.setAttribute( "src", data.data[0].card_images[0].image_url )
          return data.data[0].name
        }
      ).catch(
        (reason) => {
          console.log(reason)
        }
      ),
      this.yugiohproApi.getCardinFormat( 'goat', id )
      .toPromise()
      .then( 
        () => {
          return true; 
        }
      )
      .catch( 
        () => {
          return false; 
        } 
      )
    ])
  }
}
