import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { YuGiOhProCard } from 'src/app/models/YuGiOhProCard';

@Injectable({
  providedIn: 'root'
})
export class YuGiOhProApiService {
  apiUrl = 'https://db.ygoprodeck.com/api/v7/cardinfo.php';

  constructor(private http: HttpClient) { }
  
  getCardInfo(id) {
    return this.http.get<YuGiOhProCard>(`${this.apiUrl}?id=${id}`);
  }
  
  getCardinFormat(format, id) {
    return this.http.get<YuGiOhProCard>(`${this.apiUrl}?format=${format}&id=${id}`);
  }
}
